:root {
  --article-background-colour: #eee;
  --border-colour: #000;
  --backdrop-colour: #ccc;
  --even-table-row-colour: #ccc;
}
html, body {
  height: 100%;
  padding: 0;
  margin: 0;
  width: 100%;
}
html, body, input, textarea, select, button {background-color: var(--backdrop-colour) !important;}
body {
  background-image: url(/space.gif);
  line-height: 1.2;
  font-family: Arial, sans-serif;
  font-size: 16px;
}
header, .clearfix {overflow: auto;}
#accounts {
  display: flex;
  flex-flow: row wrap;
  gap: 16px 48px;
  margin: 0 24px;
  justify-content: center;
}
#accounts a {
  width: 72px;
  text-align: center;
}
#accounts img {
  margin: 0 auto;
  display: block;
}
img.emoji {
  height: 1em;
  width: 1em;
  margin: 0 .05em 0 .1em;
  vertical-align: -0.1em;
}
table {
  border: 1px solid var(--border-colour);
  border-collapse: collapse;
  margin: 0 auto;
  table-layout: fixed;
  max-width: 100%;
}
th {
  background-color: var(--border-colour);
  color: #fff;
}
th, td {
  border-left: 1px solid var(--border-colour);
  border-top: 1px solid var(--border-colour);
  padding: 8px;
  vertical-align: baseline;
}
tbody > tr:nth-child(even) {
  background-color: var(--even-table-row-colour);
}
.no-marker {list-style: none;}
.float-left {float: left;}
.float-right {float: right;}
figure {
  border: 1px solid var(--border-colour);
  margin: 1em 20px;
  max-width: 230px;
  padding: 4px;
}
figure.noborder {
  border: none;
}
figure.maxwidth {
  max-width: none;
}
figcaption {
  font-size: 12px;
  text-align: center;
}
blockquote {
  border-left: 6px solid var(--border-colour);
  float: left;
  margin: 1em 2em 1em 1em;
  max-width: -webkit-fit-content;
  max-width: -moz-fit-content;
  max-width: fit-content;
  padding-left: 1em;
}
cite {
  display: block;
}
p.center {
  text-align: center;
}
div.center {
  margin: 0 auto;
  max-width: -webkit-fit-content;
  max-width: -moz-fit-content;
  max-width: fit-content;
  &, & > * {
    clear: both;
    float: none;
  }
}

main {
  display: flex;
  flex-direction: column;
  height: 100%;
}
main > * {
  padding: 8px;
}
article {
  background-color: var(--article-background-colour);
  border-top: 1px solid var(--border-colour);
  border-bottom: 1px solid var(--border-colour);
  flex: 1 1 auto;
  overflow-y: auto;
}
article img {
  max-width: 100%;
}
p {
  overflow-wrap: break-word;
}
p.no-margin {
  margin: 0;
}
p.no-bottom-margin {
  margin-bottom: 0;
}
p.indent-paragraph { 
  text-indent: 2.5em;
}
p.justify-text {
  text-align: justify;
}
header {
  border-bottom: 1px solid var(--border-colour);
}
footer {
  flex-shrink: 0;
  text-align: right;
}
nav, footer {
  color: #eee;
  font-size: small;
 	a {color: #3391ff;}
  a:visited {color: #ee48ee;}
}

@media (orientation: portrait) {
  #home-table div:first-child {
    border-bottom: 3px solid var(--border-colour);
  }
  #home-table div:last-child {
    padding-left: none;
    padding-top: 8px;
  }
}

@media (orientation: landscape) {
  #home-table {
    display: flex;
    height: 100%;
  }
  #home-table > div:first-child {
    flex: 3;
  }
  #home-table > div.spacer {
    border-left: 1px solid var(--border-colour);
    flex: 0;
    margin: 0 8px;
  }
  #home-table > div:last-child {
    flex: 7;
  }
}

@media (orientation: landscape) and (min-width: 1280px) {
  article {
    border-left: 1px solid var(--border-colour);
    border-right: 1px solid var(--border-colour);
  }
  body {
    align-items: center;
    display: flex;
    justify-content: center;
  }
  main {
    height: 80%;
    width: 60%;
  }
}

@media (prefers-color-scheme: dark) {
  :root {
    --article-background-colour: #222;
    --border-colour: #888;
    --backdrop-colour: #111;
    --even-table-row-colour: #111;
  }
	body, input, textarea, select, button {
		border-color: #736b5e;
		color: #e8e6e3;
	}
	a {color: #3391ff;}
  a:visited {color: #ee48ee;}
}
